/* circularStd */
@font-face {
  font-family: 'CircularStd-Book';
  src: url(/static/media/CircularStd-Book.1d4059ce.eot);
  src: url(/static/media/CircularStd-Book.1d4059ce.eot?#iefix) format('embedded-opentype'),
       url(/static/media/CircularStd-Book.f053c365.svg#CircularStd-Book) format('svg'),
       url(/static/media/CircularStd-Book.43f768f8.ttf) format('truetype'),
       url(/static/media/CircularStd-Book.d356ee6e.woff) format('woff'),
       url(/static/media/CircularStd-Book.36b95d10.woff2) format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* circularStdBold */
@font-face {
  font-family: 'CircularStd-Bold';
  src: url(/static/media/CircularStd-Bold.56f82cac.eot);
  src: url(/static/media/CircularStd-Bold.56f82cac.eot?#iefix) format('embedded-opentype'),
       url(/static/media/CircularStd-Bold.ec482ba4.svg#CircularStd-Bold) format('svg'),
       url(/static/media/CircularStd-Bold.df13ba46.ttf) format('truetype'),
       url(/static/media/CircularStd-Bold.74679a4c.woff) format('woff'),
       url(/static/media/CircularStd-Bold.fdbfc358.woff2) format('woff2');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
body {
  background: url(/static/media/bg1.a730c156.svg);
  background-color: #1E1E1E;
  min-height: 100vh;
  font-family: 'CircularStd-Book';
}
button {
  font-family: 'CircularStd-Bold';
}
h1,h2,h3,h4,h5,h6 {
  font-family: 'CircularStd-Bold';
}
label, textarea, select, option {
  font-family: 'CircularStd-Book' !important;
}
.theme-color {
  color: #1EB76E !important;
}

.theme-bg {
  background-color: #1EB76E;
}
.color-white {
  color: #fff;
}
.navlink {
  color: #1EB76E;
  font-size: 1.5rem;
  text-decoration: none;
  bottom: 20px;
}



nav {
    margin: 2rem;
}
nav span {
    font-size: 2.2rem;
}
form {
    background-color: #fff;
    padding: 1rem;
    padding-bottom: 4rem;
    margin-left: 3rem;
}
form label {
    color: #707070 !important;
}
form>div {
    width: 100%;
}
.single-line-input {
    width: 75% !important;
}
form>div>div, .single-line>div>div {
    border-bottom: 1px solid #707070;
}
form>div>div::after, .single-line>div>div::after {
    border-bottom: 1px solid #707070 !important;
}
form>div>div::before, .single-line>div>div::before {
    border-bottom: none;
}

form textarea {
    color: #707070 !important;
    width: 100% !important;
}
.track {
    padding: 10px !important;
    background-color: #f0f0f0;
    color: #707070;
    outline: none;
    height: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    margin-top: 26px;
}
.single-line {
    display: -webkit-flex;
    display: flex;
}
.single-line select, .single-line button {
    margin-left: 10px;
    /* width: 30% */
}
.add-member {
    padding: 10px !important;
    background-color: #f0f0f0;
    color: #707070;
    outline: none;
    height: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    margin-top: 26px;
    border: 1px solid #707070;
}
.proj-img {
    /* width: 400px; */
    margin-top: 20px;
    height: 35px;
    background-color: white;
    /* box-shadow: 1px 2px 3px #ededed; */
    position:relative;
    border-bottom: 2px solid #707070;
    /* display: flex; */
    width: 100%;
    /* padding: 10px; */
}
#val {
    width: 100%;
    height:35px;
    position: absolute;
    top: 0;
    left: 0;
    font-size:13px;
    line-height: 35px;
    text-indent: 10px;
    pointer-events: none;
    color: #707070;
    font-size: 16px;
}
#file-btn {
    cursor: pointer;
    display: block;
    /* width: 90px; */
    padding: 0 5px;
    height:35px;
    color: white;
    position: absolute;
    right:0;
    top: 0;
    font-size: 16px;
    line-height:35px;
    text-align: center;
    background-color: #f0f0f0;
    color: #707070;
    outline: none;
    border: 1px solid #707070;
    margin-left: 5px;
}
input[type="file"] {
    width: 100%;
    opacity: 0;
    height: 35px;
    cursor: pointer;
}

.right-text {
    margin-top: 20vh !important;
    text-align: center;
}
.view-btn, .submit-btn {
    background: #1EB76E;
    color: #fff;
    padding: 12px 40px;
    font-size: 16px;
    font-weight: bold;
    border: 1px solid #fff;
    cursor: pointer;
    outline: none;
}
.view-btn:hover, .submit-btn:hover {
    border: 1px solid #1EB76E;
    transition: ease-in .2s;
}

.submit-btn {
    padding: 10px 25px;
    position: absolute;
    margin-top: 2.5rem;
    text-align: right;
}

@media only screen and (max-width: 959px) {
    form {
        margin-left: 0;
    }
    .single-line-input {
        width: 100% !important;
    }
    .single-line {
        display: block;
    }
    .single-line select {
        width: 100%;
        margin-left: 0;
        padding: 10px;
    }
}


.tracks ul {
    list-style: none;
    background: rgba(255,255,255,.15);
    margin: 0;
    padding: 0;
}
.tracks li {
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    padding: 15px 0 15px 10px;
    cursor: pointer;
}

.tracks li:hover, .tracks li.active {
    background-color: #fff;
    color: #2DD585;
}
.project-name svg {
    height: 2.5em;
    width: 2.5em;
    color: #1EB76E;
}
.project-item {
    border-bottom: 1px solid #1E1E1E !important;
}
.project-name {
    border-bottom: 1px solid #1E1E1E !important;
}
.project-content {
    margin-left: 20px !important;
}
.loader-div {
    text-align: center;
    margin-top: 20vh;
}
.circular-loader {
    margin: 0 auto;
}
.no-submissions-p {
    background-color: #fff;
    color: #1E1E1E;
    border: 1px solid #1E1E1E;
    padding: 1rem;
    margin: 0;
}
.loader-div {
    margin-top: 20vh;
}

.right-col-pr {
    margin-left: 20px !important;
}

@media only screen and (max-width: 959px) {
    .project-content {
        margin-left: 0 !important;
        margin-top: 10px !important;
    }
    .loader-div {
        margin-top: 0;
    }
    .right-col-pr {
        margin-left: 0 !important;
    }
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

